import { Component, OnInit } from '@angular/core';
import { trigger, style, animate, transition } from '@angular/animations';

import { PopupService } from './../../core/services/popup.service';
import { ModalService } from './../../core/services/modal.service';
import { LangService } from './../../core/services/references/lang.service';
import { Router } from '@angular/router';
import { UserService } from '@services/user.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'itx-picture-user',
  templateUrl: './upload-picture.component.html',
  styleUrls: ['./upload-picture.component.scss'],
  animations: [
    trigger('blockState', [
      transition('void => *', [
        style({ bottom: '-100%' }),
        animate('250ms ease-out', style({ bottom: '0' }))
      ]),
      transition(':leave', [
        style({ bottom: '0' }),
        animate('250ms ease-in', style({ bottom: '-100%' }))
      ]),
    ])
  ]
})
export class UploadPictureComponent implements OnInit {

  public translation;
  public userInfos;
  public uploadPicture: boolean;
  public onLoad: boolean = false;

  public uploadForm: FormGroup;
  public inputFile: FormControl;

  constructor(
    private popupService: PopupService,
    private modalService: ModalService,
    private langService: LangService,
    private router: Router,
    private userService: UserService
  ) {
    this.translation = this.langService.lang;
    this.inputFile = new FormControl(null, [Validators.required]);
    this.uploadForm = new FormGroup({
      files: this.inputFile
    });

    this.modalService.modalState$.subscribe(res => {
      this.uploadPicture = res;
    });

    this.userService.userInfos$.subscribe((userInfos) => {
      this.userInfos = userInfos;
    });
  }

  ngOnInit(): void {
    const popupState: boolean = this.popupService.popupState;
    if (popupState) {
      this.popupService.setPopupState(false);
    }
    this.userService.getUserInfos().subscribe();
  }

  public navigateTo(route: string): void {
    this.router.navigate([{ outlets: { modal: null } }], { skipLocationChange: true, queryParams: { skipLocationChange: true } }).then(res => {
      this.router.navigate([route]);
    });
    // this.router.navigate([route]);
  }

  public submitPic(): void {
    this.onLoad = true;
    this.userService.uploadPictureUser(this.uploadForm.value.files[0]).subscribe(res => {
      this.onLoad = false;
      this.userService.getPictureUser().subscribe();
      this.navigateTo('/');
    });
  }

}
